import { LockOutlined, UserOutlined } from "@ant-design/icons"
import { Button, Form, Input, message, Alert, Card, Space } from "antd"
import React, { useRef, useState, useEffect, useCallback } from "react"
import styles from './style/index.module.less'
import useLocale from "@/hooks/useLocale"
import service from "@/utils/api"
import useStorage from "@/utils/useStorage"
import { useNavigate } from 'react-router-dom'

// 
type PageStatus = 'checking' | 'first-use' | 'token-login' | 'login'

export default function LoginForm() {
    const formRef = useRef(null)
    const [errorMessage, setErrorMessage] = useState('')
    const [loading, setLoading] = useState(false)
    const [pageStatus, setPageStatus] = useState<PageStatus>('checking') //  checking
    const [loginParams, setLoginParams, removeLoginParams] = useStorage('loginParams')
    const [rememberPassword,] = useState(!!loginParams)
    const t = useLocale()
    const navigate = useNavigate()

    // Token
    const validateTokenAndProceed = useCallback((tokenOverride?: string) => {
        console.log('[Login Form]: validateTokenAndProceed - Token');
        const currentToken = tokenOverride || localStorage.getItem('hexoProToken');
        console.log(`[Login Form]: validateTokenAndProceed - Token (override: ${!!tokenOverride}):`, currentToken?.substring(0, 20) + '...');
        if (!currentToken) {
            console.log('[Login Form]: validateTokenAndProceed - Token');
            setPageStatus('login');
            return;
        }

        console.log('[Login Form]: validateTokenAndProceed - Token');
        const requestConfig: { headers?: { [key: string]: string } } = {};
        if (tokenOverride) {
            requestConfig.headers = { 'Authorization': 'Bearer ' + tokenOverride };
            console.log('[Login Form]: validateTokenAndProceed -  /userInfo  Authorization ');
        }

        service.get('/hexopro/api/userInfo', requestConfig)
            .then(res => {
                if (res.data && res.data.code !== 401 && res.data.name) { // 
                    console.log('[Login Form]: validateTokenAndProceed - Token', res.data);
                    if (typeof navigate === 'function') {
                        navigate('/pro');
                    } else {
                        window.location.href = '/pro';
                    }
                } else {
                    console.log('[Login Form]: validateTokenAndProceed - Token', res.data);
                    localStorage.removeItem('hexoProToken');
                    setPageStatus('login');
                }
            })
            .catch((err) => {
                console.log('[Login Form]: validateTokenAndProceed - Token', err);
                localStorage.removeItem('hexoProToken');
                setPageStatus('login');
            });
    }, [navigate]); // useCallback navigate

    // 
    const checkFirstUse = useCallback(async () => {
        try {
            console.log('[Login Form]: ');
            const res = await service.get('/hexopro/api/settings/check-first-use');
            if (res.data && res.data.code === 0) {
                const { isFirstUse, hasTemporaryUser } = res.data.data;
                console.log('[Login Form]: :', { isFirstUse, hasTemporaryUser });
                
                if (isFirstUse) {
                    // 
                    setPageStatus('first-use');
                } else {
                    // token
                    const token = localStorage.getItem('hexoProToken');
                    if (token) {
                        validateTokenAndProceed(token);
                    } else {
                        setPageStatus('login');
                    }
                }
            } else {
                console.log('[Login Form]: ');
                setPageStatus('login');
            }
        } catch (error) {
            console.error('[Login Form]: :', error);
            setPageStatus('login');
        }
    }, [validateTokenAndProceed]);

    // 
    useEffect(() => {
        console.log('[Login Form Effect]:  useEffect');
        
        if (window.location.pathname.includes('/pro/login')) {
            const urlParams = new URLSearchParams(window.location.search);
            const reason = urlParams.get('reason');
            if (reason) {
                console.log('[Login Form Effect Browser]: reason:', reason);
                checkFirstUse();
                return;
            }
        }
        
        // 
        checkFirstUse();
    }, [checkFirstUse]); // checkFirstUse 

    //  - 
    const handleSetupNow = () => {
        console.log('[Login Form]: ');
        
        //  token userInfo API 
        localStorage.removeItem('hexoProToken');
        
        //  window.location.href 
        // 
        window.location.href = '/pro/settings';
    };

    //  - 
    const handleSetupLater = async () => {
        console.log('[Login Form]: ');
        setLoading(true);
        
        try {
            // API
            const res = await service.post('/hexopro/api/settings/skip-setup');
            if (res.data && res.data.code === 0) {
                console.log('[Login Form]: ');
                // token
                if (res.data.data && res.data.data.token) {
                    localStorage.setItem('hexoProToken', res.data.data.token);
                }
                message.success(t['settings.setupLaterMessage']);
                
                // 
                setTimeout(() => {
                    if (typeof navigate === 'function') {
                        navigate('/pro');
                    } else {
                        window.location.href = '/pro';
                    }
                }, 1000);
            } else {
                message.error(res.data?.msg || '');
                setPageStatus('login');
            }
        } catch (error) {
            console.error('[Login Form]: :', error);
            message.error('');
            setPageStatus('login');
        } finally {
            setLoading(false);
        }
    };

    // 
    function afterLoginSuccess(params, token) {
        console.log('[Login Form]: token', token);
        
        // 
        if (rememberPassword) {
            setLoginParams(JSON.stringify(params))
        } else {
            removeLoginParams()
        }
        
        // token
        if (token) {
            localStorage.setItem('hexoProToken', token);
            if (window.isHexoProDesktop) {
                service.post('/hexopro/api/desktop/save-token', { token: token })
                .then(res=>{
                    // console.log('[Login Form]: token (after explicit login):', JSON.stringify(res));
                })
            }
            // token API ()
            if (window.electronAPI) { //  electronAPI 
                //  window.isHexoProDesktop
                fetch('/hexopro/api/desktop/save-token', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ token })
                })
                .then(response => response.json())
                .then(result => {
                    console.log('[Login Form]: token (after explicit login):', result);
                    // 
                })
                .catch(error => {
                    console.error('[Login Form]: token (after explicit login):', error);
                });
            }
        }
        
        // 
        window.location.href = '/pro';
    }

    // 
    function login(params) {
        setLoading(true);
        service.post('/hexopro/api/login', params)
            .then((res) => {
                const { code, msg, token } = res.data;
                console.log('[Login Form]: token', JSON.stringify(res));
                if (code === 0 || code === -2) {
                    afterLoginSuccess(params, token);
                } else {
                    setErrorMessage(msg || t['login.form.login.errMsg']);
                }
            })
            .catch((err) => {
                setErrorMessage(err.message || t['login.form.login.errMsg']);
            })
            .finally(() => {
                setLoading(false);
            });
    }

    // 
    function onSubmitClick() {
        formRef.current.validateFields()
            .then((values) => {
                login(values);
            })
            .catch((err) => {
                message.error(t['login.form.validate.errMsg'] + err);
            });
    }

    // 
    if (pageStatus === 'checking') {
        return (
            <div className={styles['login-form-wrapper']}>
                <div className={styles['login-form-title']}>{t['login.form.title']}</div>
                <div className={styles['login-form-sub-title']}>{t['settings.system.check.status']}</div>
            </div>
        );
    }

    // 
    if (pageStatus === 'first-use') {
        return (
            <div className={styles['login-form-wrapper']}>
                <div className={styles['login-form-title']}>{t['settings.welcomeTitle']}</div>
                <div className={styles['login-form-sub-title']}>{t['settings.welcomeSubtitle']}</div>
                
                <Alert
                    message={t['settings.firstUsePrompt']}
                    description={t['settings.firstUseDescription']}
                    type="info"
                    showIcon
                    style={{ marginBottom: 24 }}
                />
                
                <Space direction="vertical" style={{ width: '100%' }} size="large">
                    <Button 
                        type="primary" 
                        size="large" 
                        onClick={handleSetupNow}
                        loading={loading}
                        style={{ width: '100%' }}
                    >
                        {t['settings.setupNow']}
                    </Button>
                    
                    <Button 
                        size="large" 
                        onClick={handleSetupLater}
                        loading={loading}
                        style={{ width: '100%' }}
                    >
                        {t['settings.setupLater']}
                    </Button>
                </Space>
            </div>
        );
    }

    // 
    return (
        <div className={styles['login-form-wrapper']}>
            <div className={styles['login-form-title']}>{t['login.form.title']}</div>
            <div className={styles['login-form-sub-title']}>{t['login.form.subTitle']}</div>
            {errorMessage && 
                <Alert 
                    message={errorMessage} 
                    type="error" 
                    showIcon 
                    style={{ marginBottom: 20 }} 
                    closable 
                    onClose={() => setErrorMessage('')} 
                />
            }
            <Form
                onFinishFailed={() => message.error('Submit failed!')}
                ref={formRef}
                layout="vertical"
            >
                <Form.Item
                    rules={[{ required: true }]} //  required 
                    label={t['login.form.username']}
                    name="username"
                >
                    <Input prefix={<UserOutlined />} />
                </Form.Item>
                <Form.Item
                    rules={[{ required: true }]} //  required 
                    label={t['login.form.password']}
                    name="password"
                >
                    <Input.Password prefix={<LockOutlined />} />
                </Form.Item>
                <Button type="primary" onClick={onSubmitClick} loading={loading}>
                    {t['login.form.login']}
                </Button>
            </Form>
        </div>
    );
}
